const getStatusBadge = (status: string) => {
  const styles = {
    active: "bg-green-100 text-green-700 dark:bg-green-900/20 dark:text-green-400",
    inactive: "bg-gray-100 text-gray-700 dark:bg-gray-900/20 dark:text-gray-400",
    suspended: "bg-red-100 text-red-700 dark:bg-red-900/20 dark:text-red-400",
  };

  const icons = {
    active: CheckCircle2,
    inactive: Clock,
    suspended: XCircle,
  };

  const Icon = icons[status as keyof typeof icons] || Clock;

  return (
    <Badge
      variant="outline"
      className={`${styles[status as keyof typeof styles]}`}
    >
      <Icon className="h-3 w-3 mr-1" />
      {status.charAt(0).toUpperCase() + status.slice(1)}
    </Badge>
  );
};

const ProvidersPage = () => {
  const router = useRouter();
  const [searchQuery, setSearchQuery] = useState("");
  const [providers, setProviders] = useState<Provider[]>(defaultProviders);
  const { toast } = useToast();

  useEffect(() => {
    if (typeof window === "undefined") return;

    const stored = localStorage.getItem(PROVIDERS_STORAGE_KEY);
    if (stored) {
      try {
        const parsed = JSON.parse(stored) as Provider[];
        if (Array.isArray(parsed) && parsed.length > 0) {
          setProviders(parsed);
        }
      } catch (error) {
        console.error("Failed to parse stored providers", error);
      }
    }
  }, []);

  useEffect(() => {
    if (typeof window === "undefined") return;
    localStorage.setItem(PROVIDERS_STORAGE_KEY, JSON.stringify(providers));
  }, [providers]);

  const filteredProviders = providers.filter(
    (provider) =>
      provider.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      provider.email.toLowerCase().includes(searchQuery.toLowerCase()) ||
      provider.specialization.toLowerCase().includes(searchQuery.toLowerCase())
  );


  const stats = [
    {
      title: "Total Providers",
      value: providers.length,
      icon: UserCog,
      color: "text-blue-600",
      bgColor: "bg-blue-100 dark:bg-blue-900/20",
    },
    {
      title: "Active Providers",
      value: providers.filter((p) => p.status === "active").length,
      icon: CheckCircle2,
      color: "text-green-600",
      bgColor: "bg-green-100 dark:bg-green-900/20",
    },
    {
      title: "Avg Rating",
      value: (
        providers.reduce((acc, p) => acc + p.rating, 0) / providers.length
      ).toFixed(1),
      icon: Star,
      color: "text-yellow-600",
      bgColor: "bg-yellow-100 dark:bg-yellow-900/20",
    },
    {
      title: "Total Jobs",
      value: providers.reduce((acc, p) => acc + p.completedJobs, 0),
      icon: CheckCircle2,
      color: "text-cyan-600",
      bgColor: "bg-cyan-100 dark:bg-cyan-900/20",
    },
  ];

  return (
    <div className="space-y-6">
      {/* Stats Cards */}
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        {stats.map((stat, index) => {
          const Icon = stat.icon;
          return (
            <Card key={index}>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  {stat.title}
                </CardTitle>
                <div className={`p-2 rounded-lg ${stat.bgColor}`}>
                  <Icon className={`h-4 w-4 ${stat.color}`} />
                </div>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{stat.value}</div>
              </CardContent>
            </Card>
          );
        })}
      </div>

      {/* Providers Table */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle>Service Providers</CardTitle>
            <Button
              className="text-white"
              style={{
                background: "linear-gradient(135deg, #00BCD4 0%, #00D4E8 100%)",
              }}
              onClick={() => router.push("/admin/add-provider")}
            >
              <Plus className="h-4 w-4 mr-2" />
              Add Provider
            </Button>
          </div>
          <div className="flex items-center gap-2 mt-4">
            <div className="relative flex-1 max-w-sm">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search providers..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Name</TableHead>
                <TableHead>Contact</TableHead>
                <TableHead>Specialization</TableHead>
                <TableHead>Rating</TableHead>
                <TableHead>Completed Jobs</TableHead>
                <TableHead>Status</TableHead>
                <TableHead className="text-right">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredProviders.map((provider) => (
                <TableRow key={provider.id}>
                  <TableCell>
                    <div className="flex items-center gap-2">
                      <div className="h-8 w-8 rounded-full bg-gradient-to-br from-cyan-400 to-blue-500 flex items-center justify-center text-white font-semibold text-sm">
                        {provider.name
                          .split(" ")
                          .map((n) => n[0])
                          .join("")}
                      </div>
                      <Link href={`/admin/providers/${provider.id}`} className="font-medium text-cyan-700 hover:underline">
                        {provider.name}
                      </Link>
                    </div>
                  </TableCell>
                  <TableCell>
                    <div className="space-y-1">
                      <div className="flex items-center gap-1 text-sm">
                        <Mail className="h-3 w-3 text-muted-foreground" />
                        <Link href={`/admin/providers/${provider.id}`} className="text-cyan-700 hover:underline">
                          {provider.email}
                        </Link>
                      </div>
                      <div className="flex items-center gap-1 text-sm">
                        <Phone className="h-3 w-3 text-muted-foreground" />
                        <span className="text-muted-foreground">
                          {provider.phone}
                        </span>
                      </div>
                    </div>
                  </TableCell>
                  <TableCell>
                    <Badge variant="secondary">{provider.specialization}</Badge>
                  </TableCell>
                  <TableCell>
                    <div className="flex items-center gap-1">
                      <Star className="h-4 w-4 fill-yellow-400 text-yellow-400" />
                      <span className="font-medium">{provider.rating}</span>
                    </div>
                  </TableCell>
                  <TableCell>{provider.completedJobs}</TableCell>
                  <TableCell>{getStatusBadge(provider.status)}</TableCell>
                  <TableCell className="text-right">
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button variant="ghost" size="icon">
                          <MoreVertical className="h-4 w-4" />
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent align="end">
                        <DropdownMenuLabel>Actions</DropdownMenuLabel>
                        <DropdownMenuSeparator />
                        <DropdownMenuItem onClick={() => router.push(`/admin/providers/${provider.id}`)}>
                          View Details
                        </DropdownMenuItem>
                        <DropdownMenuItem onClick={() => router.push(`/admin/providers/${provider.id}`)}>
                          Edit Provider
                        </DropdownMenuItem>
                        <DropdownMenuItem onClick={() => {
                          toast({
                            title: "View Jobs",
                            description: `Viewing jobs for ${provider.name}`,
                          });
                        }}>
                          View Jobs
                        </DropdownMenuItem>
                        <DropdownMenuSeparator />
                        <DropdownMenuItem 
                          className="text-red-600"
                          onClick={() => {
                            const newStatus: ProviderStatus = provider.status === 'suspended' ? 'active' : 'suspended';
                            setProviders(providers.map(p => 
                              p.id === provider.id 
                                ? { ...p, status: newStatus }
                                : p
                            ));
                            toast({
                              title: newStatus === 'suspended' ? "Provider Suspended" : "Provider Activated",
                              description: `${provider.name} has been ${newStatus === 'suspended' ? 'suspended' : 'activated'}.`,
                            });
                          }}
                        >
                          {provider.status === 'suspended' ? 'Activate Provider' : 'Suspend Provider'}
                        </DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>

    </div>
  );
};

export default ProvidersPage;
